.main{
    margin-left: 200px;
    width: 100vw;
    height: 100%;
    padding-left: 20px;
    background: linear-gradient(180deg, #1CB5E0 0%, #000851 100%);
    transition: 0.6s;
}

.header_{
    display: flex;
    flex-direction: column;
}

.title_{
    color: white;
    font-size: 21px;
    line-height: 60px;
    margin-top: 20px;
    margin-left: 6px;
    display:inline-block
}

.itemGroup{
    display: inline-block;
    background-color: rgba(0,0,0,0.5);
    line-height: 26px;
    width: 200px;
    display: flex;
    flex-direction: row;
    border-radius: 20px;
    box-sizing: border-box;
}

.items_{
    flex:1;
    text-align: center;
    color: white;
    font-size: 10px;
    user-select: none;
    border-radius: 20px;
    margin: 5px;
    transition: .6s;
}

.container{
    margin-top: 20px;
}

.card{
    margin:10px;
    box-sizing: border-box;
    background-color: rgba(255,255,255,0.8);
    height: 300px;
}