@import url(https://fonts.googleapis.com/css?family=Roboto);
*{
    list-style: none;
    margin: 0;
    padding:0;
    font-family: Roboto;
}

.header{
    background-color: rgb(39, 51, 82);
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.navigation{
    background-color: #212B45;
    width: 200px;
    height: 100vh;
    position: fixed;
    -webkit-transition: 0.6s;
    transition: 0.6s;
}

.title{
    color: white;
    text-align: center;
    padding-top: 40px;
}

.version{
    color:rgba(196, 94, 54,0.5);
    text-align: center;
    line-height: 20px;
    font-size: 11px;
    margin-top: 8px;
    padding-bottom: 15px;
}

ul li{ 
    color: white;
    font-size: 14px;
    line-height: 67px;
    padding-left: 30px;
    border-bottom: 0.1px solid rgba(245,245,245,0.2);
    border-top: 0.1px solid rgba(0,0,0,0.2);
    -webkit-transition: .5s;
    transition: .5s;
}

li i{
    margin-right: 14px;
}

li:hover{
    padding-left: 38px;
}
.main{
    margin-left: 200px;
    width: 100vw;
    height: 100%;
    padding-left: 20px;
    background: -webkit-gradient(linear, left top, left bottom, from(#1CB5E0), to(#000851));
    background: linear-gradient(180deg, #1CB5E0 0%, #000851 100%);
    -webkit-transition: 0.6s;
    transition: 0.6s;
}

.header_{
    display: flex;
    flex-direction: column;
}

.title_{
    color: white;
    font-size: 21px;
    line-height: 60px;
    margin-top: 20px;
    margin-left: 6px;
    display:inline-block
}

.itemGroup{
    display: inline-block;
    background-color: rgba(0,0,0,0.5);
    line-height: 26px;
    width: 200px;
    display: flex;
    flex-direction: row;
    border-radius: 20px;
    box-sizing: border-box;
}

.items_{
    flex:1 1;
    text-align: center;
    color: white;
    font-size: 10px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    border-radius: 20px;
    margin: 5px;
    -webkit-transition: .6s;
    transition: .6s;
}

.container{
    margin-top: 20px;
}

.card{
    margin:10px;
    box-sizing: border-box;
    background-color: rgba(255,255,255,0.8);
    height: 300px;
}
